@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    .footer_logo > img {
        height: 40px;
        width: 40px;
    }
    .footer_section {
        padding: 1.7rem;
        margin-top: 0rem;
    }
    .footer_logo {
        display: flex;
        align-items: center;
        gap: 1rem;
    }

    .footer_logo > h1 {
        font-size: 19.5px;
    }
    .footer_logo > h1 > sub {
        display: none;
    }
    #description {
        margin-top: 0px;
        text-align: left;
        margin-bottom: 1rem;
    }

    #description > p {
        font-size: 14px;
        line-height: 1.5rem;
        text-align: center;
    }

    .social_icons {
        margin: 3px;
    }
}
