@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    /* Styles */
    #background {
        top: -60vh;
        left: -91vh;
    }
    #card {
        margin-bottom: 9rem;
    }
    #background::after {
        right: -90vh;
        bottom: -70vh;
        width: inherit;
        height: inherit;
    }
    #card > h2 {
        margin-top: 3vh;
    }
    #loginForm {
        margin-top: 0.1vh;
    }
}
